							<vs-th v-for="(header, index) in headers" :key="index">
								<vs-col vs-type="flex" vs-align="center">
									<span class="uppercase mr-2">{{ header }}</span>
									<!-- <vs-dropdown vs-custom-content vs-trigger-click>
										<vs-button color="#626262" type="flat" icon="filter_list" class="filter-btn"></vs-button>
										<vs-dropdown-menu>
											<div class="filter-popup">
												<h3>Contains</h3>
												<v-select multiple :closeOnSelect="false" class="filter-popup__select" v-model="selected" :options="options" />
											</div>
										</vs-dropdown-menu>
									</vs-dropdown> -->
								</vs-col>
							</vs-th>
						</template>
						<template slot-scope="{data}">
							<vs-tr
								:key="indextr"
								v-for="(tr, indextr) in data"
								@click.native="$router.push({ name: 'details' })"
								class="pointer"
								:class="{'read': data[indextr].read}"
							>
								<vs-td :data="data[indextr].received">
									{{data[indextr].received}}
								</vs-td>
								<vs-td :data="data[indextr].pick_up_at">
									<vs-row vs-align="center">
										<span class="pr-2">{{data[indextr].pick_up_at}}</span>
