								</vs-td>
								<vs-td :data="data[indextr].truck_type">
									<div class="flex items-center">
										<span :class="data[indextr].truck_icon" class="mr-2"></span>
										<span>{{data[indextr].truck_type}}</span>
									</div>
								</vs-td>
								<vs-td :data="data[indextr].weight">
									{{data[indextr].weight}} lbs
								</vs-td>
								<vs-td :data="data[indextr].distance">
									<span>{{data[indextr].distance.miles}}mi</span>
                    				<vs-progress :percent="data[indextr].distance.percent" :color="data[indextr].distance.color"></vs-progress>
								</vs-td>
								<vs-td :data="data[indextr].matches">
									{{data[indextr].matches}} (<i class="material-icons plane-icon">local_airport</i> 15mi)
								</vs-td>
								<vs-td :data="data[indextr].brokerage">
									{{data[indextr].brokerage}}
								</vs-td>
								<vs-td :data="data[indextr].posting_exp">
